let isSupportWebpGoal = null;

const isSupportWebp = () => {
    if (isSupportWebpGoal === true) {
        return true;
    }

    if (isSupportWebpGoal === false) {
        return false;
    }

    try {
        const isSupportWebp =
            document.createElement('canvas').toDataURL('image/webp', 0.5).indexOf('data:image/webp') === 0;
        const regexSafari = navigator.userAgent.match(/version\/([\d\.]+).*?safari/i);

        // Safari浏览器版本判断
        if(!isSupportWebp && regexSafari && regexSafari[1]) {
            const isMobile = navigator.userAgent.toLocaleLowerCase().includes('iphone');
            const version = parseInt(regexSafari[1]) || 0;

            if (isMobile && version >= 14) {
                isSupportWebpGoal = true;
    
                return true;
            }

            if(!isMobile && version >= 16) {
                isSupportWebpGoal = true;
    
                return true;
            }
        }

        isSupportWebpGoal = isSupportWebp;

        return isSupportWebp;
    } catch (err) {
        isSupportWebpGoal = false;

        return false;
    }
};

/**
 * 将前面的裁图前缀,参数以及协议头去掉
 * @param {*} url 要清洗的 url
 */
const clear = url => {
    try {
        if (!url) {
            return url;
        }
        if (url.includes('d.ifengimg.com/')) {
            url = url.split('d.ifengimg.com/').pop();
            const paths = url.split('/');

            paths.shift();
            url = paths.join('/');
        }

        if (url.includes('http://')) {
            url = url.substring(7);
        }

        if (url.includes('https://')) {
            url = url.substring(8);
        }

        if (url.includes('//')) {
            url = url.substring(2);
        }

        return url;
    } catch (error) {
        console.error(error);

        return url;
    }
};

