import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import errorBoundary from "@src/components/errorBoundary"
import styles from './index.css';
import { ListStore } from '../../store';
// 拉端
// import { callApp } from '@ifeng/callapp';
import { setAppQueryNoIfengsp } from '@src/utils/url';
import { devPath } from '@src/constants';
const Brand = ({ url = window.location.href, needPlaceHolder = true }) => {
    // 列表数据
    const useListStore = ListStore.useContainer();
    const { listStates, changeListStates } = useListStore;
    const { currentVideo, dramaSeries, positive } = listStates;
    const brandRef = useRef();

    // 设置端外分享栏高度
    useEffect(
        () => {
            if (brandRef.current) {
                const domRef = brandRef.current;

                changeListStates({
                    brandHeight: domRef.offsetHeight,
                });
            }
        },
        [brandRef],
    );

    const handleClick = () => {
        console.log('setAppQueryNoIfengsp(url)', setAppQueryNoIfengsp(url));

        let finalUrl = '';
        const positiveIndex = positive.findIndex(item => item.base62Id === currentVideo.base62Id);

        // 分享正片
        if (positiveIndex > -1) {
            finalUrl = setAppQueryNoIfengsp(
                `${devPath}/details/${
                    dramaSeries.base62Id
                }?id=phmovie&ref=outside&type=other&backIndex=true&source=positive&positiveId=${currentVideo.base62Id}`,
            );
        } else {
            // 分享花絮
            finalUrl = setAppQueryNoIfengsp(
                `${devPath}/details/${
                    dramaSeries.base62Id
                }?id=phmovie&ref=outside&type=other&backIndex=true&source=gags&gagsId=${currentVideo.base62Id}`,
            );
        }
        // console.log('finalUrl', finalUrl);
        // callApp({
        //     docType: 'web',
        //     pid: finalUrl,
        //     kind: 'inter_da01',
        //     noKindFrom: 1,
        //     download: 'http://a.app.qq.com/o/simple.jsp?pkgname=com.ifeng.news2&ckey=CK1512057577113',
        //     downloadAd: 'https://statistics.appstore.ifeng.com/index.php/api/godownload?app_id=4&d_ch=8927',
        // });
    };

    return !window.grounds ? (
        <div ref={brandRef}>
            {needPlaceHolder ? <div className={styles.placeholder} /> : null}
            <div className={styles.wrap} onClick={handleClick} />
        </div>
    ) : null;
};

Brand.propTypes = {
    url: PropTypes.string,
    needPlaceHolder: PropTypes.bool,
};

export default errorBoundary(Brand);
