import Axios from 'axios';
import { API_URL, COMMENT_APP_ID } from '@src/constants';
import { jsonp } from '@ifeng/ui_base';
// import { Grounds } from '@ifeng/client_grounds';
import {
    getClientParams, // 客户端标参
    showInput, // 端内评论
} from '@src/utils/app';
// 弹窗
import { message as toast } from '@src/components/ModalBox';
// axios实例
export const axios = Axios.create();

// axios实例的基础路径
axios.defaults.baseURL = API_URL;

// 客户端标参
const clientParams = getClientParams();

axios.interceptors.request.use(
    (config) => {
        return { ...config, params: { ...config.params, ...clientParams } };
    },
    (err) => {
        return Promise.reject(err);
    },
);

axios.interceptors.response.use(
    (response) => {
        const { status, data } = response;

        return { ...data, httpStatus: status };
    },
    (err) => {
        return Promise.resolve(err);
    },
);

const request = async ({ url, params, name }) => {
    if (window.grounds) {
        return await Grounds.nativeRequest({
            // eslint-disable-next-line no-undef
            url: apiUrl + url,
            getParams: JSON.stringify(params),
            type: 'get',
        });
    } else {
        // eslint-disable-next-line no-undef
        return await jsonp(apiUrl + url, {
            data: params,
            jsonpCallback: name + Date.now(),
            cache: false,
        });
    }
};

