// 获取ifeng图片尺寸
export const getIfengImageSize = (url) => {
    const pattern = /https?:\/\/.+_w(\d+)_h(\d+)\..+/g;
    const [, width, height] = pattern.exec(url);

    return [parseInt(width), parseInt(height)];
};
