// 判断是否在端内,开发环境置为true，便于开发
export const isClient = process.env.NODE_ENV === 'development' ? true : !!window.grounds;

// 是否在客户端内
export const isGrounds = !!window.grounds;

/**
 * @description 拉起端内登录
 */
export const userLogin = () => {
    if (isGrounds) {
        window.grounds.userLogin();
    }
};

/**
 * @description 客户端获取用户登陆状态
 * @returns {boolean} true: 已登陆 | false: 未登录
 */
export const userIsLogin = () => {
    return !!window?.grounds?.userIsLogin();
};

/**
 * @description 客户端关闭当前webview
 */
export const groundsClose = () => {
    if (isGrounds) {
        window.grounds.close();
    }
};

/**
 * @description 客户端打开分享托盘(弃用)
 */
export const openAppShare = ({
    url = `${window.location.origin}${window.location.pathname}`,
    title = '',
    desc = '',
    thumbnail = '',
    documentId = '',
}) => {
    if (isGrounds) {
        const appQueryUrl = setAppQuery(url);

        window.grounds.openShare(appQueryUrl, title, desc, thumbnail, documentId);
    }
};

/**
 * @description 客户端打开分享托盘（新版：分享JOSN传参）
 * title标题
 * shareurl 分享链接
 * desc 简介
 * ios用thumb /安卓用thumbnail 缩略图
 * documentId 文章id
 * type 文章类型
 */
export const newShare = (params) => {
    const appQueryUrl = setAppQuery(params.url);

    if (isGrounds) {
        const paramsDefault = {
            shareurl: `${window.location.origin}${window.location.pathname}`,
            title: '',
            desc: '',
            thumb: '',
            documentId: '',
            type: '',
        };
        let paramsValue = {
            ...paramsDefault,
            ...params,
            thumb: params.thumbnail, // ios
            thumbnail: params.thumbnail, // android
            shareurl: appQueryUrl,
        };

        paramsValue = JSON.stringify(paramsValue);

        window.grounds.newShare(paramsValue);
    }
};
// 客户端获取状态栏高度
export const getStatusBarHeight = () => {
    if (!window.grounds || !window.grounds.getStatusBarHeight) {
        return 0;
    }

    const ratio = isIOS() ? 1 : window.devicePixelRatio;

    return window.grounds.getStatusBarHeight() / ratio;
};

