export const moblieLogin = () => {
    try {
        // 有没有实名，有代表已经绑定了手机号
        if (!getUserInfoApi.getPhoneNum()) {
            // 有没有登录，有代表已经登录但没有绑定
            if (getUserInfoApi.getGuid()) {
                window.grounds.userBind();
            } else {
                userLogin();
            }

            return false;
        }

        return true;
    } catch (e) {
        console.log(e);
    }
};

// 监听页面离开
export const setPageNoActiveListener = (callback) => {
    // 安卓 webview 无法监听 visibilitychange 事件
    const isAndroid = /android/i.test(window.Grounds?.getDeviceInfo && window.Grounds.getDeviceInfo().getOs());

    if (window.grounds && isAndroid) {
        // window.grounds.on('active', 'historyTask_reGetData');
        window.grounds.on('onExit', 'historyTask_reGetData');
        window.historyTask_reGetData = () => callback();
    } else {
        document.addEventListener('visibilitychange', (e) => {
            if (document.visibilityState === 'hidden') {
                callback();
            }
        });
    }
};

// 禁止默认收拾区域，三个参数分别代表左上顶点纵坐标，左下顶点纵坐标， 屏幕高度
export const setH5PageTouchArea = (top = 0, bottom, height) => {
    try {
        // eslint-disable-next-line no-unused-expressions
        window.grounds?.setH5PageTouchArea && window.grounds.setH5PageTouchArea(top, bottom, height);
    } catch (e) {
        console.log(e);
    }
};

export const setNoSlideArea = (area) => {
    try {
        const data = JSON.stringify({
            area,
            totalHeight: window.innerHeight,
        });

        // eslint-disable-next-line no-unused-expressions
        window?.grounds?.setNoSlideArea && window?.grounds.setNoSlideArea(data);
    } catch (e) {
        console.log(e);
    }
};
/*
方法：window.grounds.openInput(options, callback);
options:
{
    value(string):打开输入框时，传入的文字
    placeholder(string):输入框的提示文字
    additional(enum):["photo","album","emoji", "gif", "audio"] 相机、相册、表情、动图、音频输入
    maxSize(number):最大输入文字长度
    callback(data):点击输入框，成功（文字不超出最大值，图片上传成功），客户端会回调该函数，并返回值。
    text(string):其中，emoji和gif都会以ubb的形式插入到text中
    image(string):上传的图片地址(图片只能上传一张。)
}
*/
// 端内原生评论
export const showInput = ({ replyName, callback }) => {
    const maxSize = 200;
    const config = {
        placeholder: replyName ? `回复${replyName}` : '请输入友善的评论',
        maxSize,
        limit: maxSize,
        additional: ['photo', 'album', 'emoji', 'gif', 'audio'],
    };

    if (isGrounds) {
        // 用户登陆
        if (userIsLogin()) {
            if (window.grounds.openInput || window.grounds.input) {
                // 声明回调（挂载window）方便使用
                const groundsCallbackName = 'inputFromClient';

                window[groundsCallbackName] = (data) => {
                    if (data && callback) {
                        callback(data);
                    }
                };

                if (window.grounds.openInput) {
                    window.grounds.openInput(JSON.stringify(config), groundsCallbackName);
                } else {
                    window.grounds.input(JSON.stringify(config), groundsCallbackName);
                }
            } else {
                // 先不考虑低版本客户端和端外的输入框
                // customModal(Input, { callback, config });
            }
        } else {
            userLogin();
        }
    } else {
        // 端外不会出现回复评论功能
    }
};

// 获取收藏状态
export const isCollected = (callback) => {};

// 收藏
/*
documentId, //  {string} 剧集id
id, // {string} 剧集id
subtype, // {string} 收藏分类 juji
title, //  {string} 剧集标题
thumbnail,  // {string} 缩略图
links : [{ // 跳转 {array->object}
    type, // {string} 跳转类型
    url // {string} 跳转协议
}],
*/
export const setCollect = ({ documentId, id, subtype, title, thumbnail, links: [{ type, url }] }, callback) => {
    const config = {
        documentId: '', //  {string} 剧集id
        id: '', // {string} 剧集id
        subtype: '', // {string} 收藏分类 juji
        title: '', //  {string} 剧集标题
        thumbnail: '', // {string} 缩略图
        links: [
            {
                // 跳转 {array->object}
                type: '', // {string} 跳转类型
                url: '', // {string} 跳转协议
            },
        ],
    };
    // 声明回调（挂载window）方便使用
    const groundsCallbackName = 'collectClient';

    window[groundsCallbackName] = (data) => {
        if (data && callback) {
            callback(data);
        }
    };

    if (window.grounds.collect) {
        window.grounds.collect(JSON.stringify(config), groundsCallbackName);
    }
};
